@media all and (min-width: 1250px) and (max-width: 1400px) {
}
@media all and (max-width: 1430px) {
    .container {
        box-sizing: border-box;
        padding: 10px 20px;
    }
    .about_us__full_container {
        left: 5% !important;
        top: 50%;
        width: 90% !important;
    }
}
@media all and (max-width: 1430px) and (min-width: 1000px) {
    .send_request__container {
        left: 20%;
    }
}
@media all and (max-width: 1300px) {
    .services__full_block_content {
        width: 90%;
    }
    .header__container {
        margin-top: 0;
        padding-top: 10px;
    }
    .header__nav {
        overflow: hidden;
        transform-origin: 0 0;
    }
    .nav__content {
        transform: translateY(-1550%);
        transform-origin: 0 0;
        transition: all 0.8s;
    }
    .header.open {
        .header__nav {
        }
        .nav__content {
            transform: translateY(0%);
            display: block;
            width: max-content;
            margin: 0 auto;
            text-align: center;
        }
        .nav__item {
            &:not(:last-child) {
                margin: 60px 0;
            }
            a {
                font-size: 20px;
            }
        }
    }
}

@media all and (max-width: 1500px) {
    .about__plane {
        right: 0;
    }
    .projects__blocks {
        justify-content: center;
    }
    .projects__block {
        margin: 20px;
    }
    .projects__blocks {
    }
    .projects__slider {
    }
    .projects__full_block {
        height: 600px;
        width: max-content !important;
        img {
            width: 70%;
            margin: 0 auto;
        }
    }
}
@media all and (max-width: 1600px) {
    .projects__container {
        margin: 0 auto;
        padding: 10px;
    }
}
@media all and (max-width: 1800px) {
    .projects__content {
        padding: 40px 50px 50px 70px;
    }
    .about_us__full_container {
        left: 10%;
        top: 50%;
    }
    .services__full_block_content {
    }
}
@media all and (max-width: 1800px) {
    .projects__content {
        padding: 40px 50px 50px 40px;
    }
}
@media all and (max-width: 500px) {
    .projects__top_right {
        justify-self: center;
        margin-top: 50px;
    }
    .projects__link {
        text-align: start;
    }
}
.projects__link {
    text-align: end;
}
@media all and (max-width: 1000px) and (min-width: 550px) {
    .projects__full_blocks {
        margin-left: 35px !important;
    }
}
@media all and (max-width: 550px) {
    .projects__full_blocks {
        margin-left: 15px !important;
    }
}
