.footer {
}
.footer__top {
    background: linear-gradient(180deg, #fcd507 0%, #fcd507 100%);
    padding: 50px 0;
}
.footer__content {
    max-width: 1196px;
    margin: 0 auto;
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    justify-content: center;
    padding: 0 15px;
    & > *:not(:last-child) {
        margin: 15px 55px 15px 0;
    }
}
.footer__block {
    display: flex;
}
.footer__block_img {
    background: #000;
    border-radius: 50%;
    width: 40px;
    height: 40px;
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 0 20px 0 0;
}
.footer_block_right {
}
.footer__title {
    font-family: "Ubuntu";
    font-style: normal;
    font-weight: bold;
    font-size: 18px;
    line-height: 21px;
    color: #000000;
}
.footer__text {
    font-family: "Ubuntu";
    font-style: normal;
    font-weight: normal;
    font-size: 16px;
    line-height: 18px;
    color: #8a7339;
}
.button {
}
.footer__button {
    background: #000;
    color: $col;
    width: 238px;
}
.footer__bottom {
    background: #000;
    padding: 12px 0;
}
.footer__link {
    font-size: 14px;
    line-height: 17px;
    text-align: center;
    color: #ffffff;
}
