@media all and (max-width: 420px) {
    .header__button {
        width: 250px;
    }
    .services__full_block_top {
        flex-wrap: wrap;
        justify-content: center;
    }
    .services__full_block_text {
        font-size: 12px;
    }
    .maint__section_bigcircle {
        width: 50px;
        height: 50px;
        bottom: -10%;
        left: 5%;
    }
    .maint__section_circle {
        width: 25px;
        height: 25px;
        bottom: -10%;
        left: 15%;
    }

    .projects__link {
        font-size: 14px;
    }
    .projects__dots {
        width: 15%;
        top: 10%;
        left: 0;
    }
    .questions__left {
        text-align: center;
    }
    .projects__content {
        padding: 0;
    }
    .projects__block_content {
        padding: 10px;
    }
    .radio label::after {
        left: 1%;
    }
    .send_request__content {
        padding: 0 15px 50px;
    }
}
