@media all and (min-width: 1000px) and (max-width: 1250px) {
}
@media all and (max-width: 1150px) {
    .qustions__content {
        grid-template-columns: repeat(1, 1fr);
        justify-items: center;
    }
    .questions__left {
        max-width: 500px;
        margin: 20px 0;
    }
    .questions {
        background-position: center;
    }
    .questions__form {
        margin: 20px 0 0;
    }
    .questions__road {
        left: 13%;
    }
}
@media all and (max-width: 950px) {
    .questions__road {
        display: none;
    }
}
