@media all and (min-width: 420px) and (max-width: 800px) {
}
@media all and (max-width: 550px) {
    .radio label {
        font-size: 12px;
    }
    .projects__full_block {
        padding: 15px;
    }
    .header__logo {
        width: 70%;
        img {
            width: 100%;
        }
    }
    .footer__content {
        & > * {
        }
        & > *:not(:last-child) {
            margin: 15px 0;
            width: 100%;
        }
    }
    .title {
        font-size: 28px;
        line-height: 35px;
    }
    .subtitle {
        font-size: 20px;
        line-height: 24px;
    }
    .about__plane {
        width: 50%;
        top: -20px;
    }
    .about__circle {
        width: 15%;
        height: 15%;
        top: -10px;
        left: 10px;
    }
    .about__dots {
        width: 10%;
        bottom: 10px;
    }
    .questions__form {
        max-width: 100%;
    }
    .questions__form_content {
        padding: 10px 20px;
    }
    .questions__left_title,
    .questions__left_text {
        font-size: 28px;
    }
    .questions__left_subtitle {
        font-size: 14px;
    }
}
@media all and (max-width: 440px) {
    .header__logo {
        img {
            // width: 60%;
        }
    }
}
@media all and (max-width: 700px) {
    .main__section_secondary {
        width: 10%;
    }
    .maint__section_bigcircle {
        width: 100px;
        height: 100px;
        left: -14px;
        bottom: -46px;
    }
    .maint__section_circle {
        width: 50px;
        height: 50px;
    }
    .about__title {
        font-size: 14px;
    }
    .about__text {
        font-size: 14px;
    }
    .services__block {
        margin-right: 0 !important;
    }
}
@media all and (max-width: 830px) {
    .projects__top {
        grid-template-columns: repeat(1, 1fr);
        text-align: center;
    }
}
@media all and (max-width: 750px) {
    .projects__top_right {
        margin-right: 0;
    }
}
@media all and (max-width: 720px) {
    .news__block {
        margin: 30px 0px;
        &:last-child {
            margin: 30px 0;
        }
    }
    .news {
        padding: 0 10px;
        box-sizing: border-box;
    }
    .radio {
        grid-template-columns: repeat(1, 1fr);
    }
    .send_request__title {
        font-size: 28px;
        margin: 0 0 10px 0;
    }

    .send_request__container input[type="text"] {
        margin: 0 0 5px 0;
        height: 30px;
    }
    .send_request__container textarea {
        height: 70px;
    }
    .send_request__container {
        top: 15px;
    }
    .send_request__content {
        margin: -20px 0 0 0;
    }
}
