.main__section_dot-right {
    right: -10px;
    top: 15%;
}
@media all and (max-width: 1200px) {
    .main__section_left {
        margin-bottom: 50px;
    }
    .main__section {
        grid-template-columns: repeat(1, 1fr);
        justify-items: center;

        .main__section_img {
            width: 100%;
        }
        .abs {
            right: 20%;
            width: 80%;
            top: 10%;
