.services {
    margin-top: 50px;
    text-align: center;
}
.services__top {
}

.services__title {
    margin: 0 0 10px 0;
}
.services__subtitle {
    font-size: 16px;
    line-height: 19px;
    color: #000000;
}
.services__blocks {
    margin: 50px 0 50px 0;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}
.services__block_content {
    padding: 20px 35px;
}
.services__block {
    max-width: 328px;
    background: #fff;
    box-shadow: 0px 10px 30px rgba(0, 0, 0, 0.05);
    border-radius: 0px 50px;
    margin: 20px 0;
    cursor: pointer;
    transition: all 0.2s ease-in-out;
    &:not(:last-child) {
        margin-right: 29px;
    }
    &:hover {
        background: $col;
        .services__block_img {
            background: black;
        }
