.active_request {
    display: block !important;
    z-index: 1000;
}
.send_request {
    display: none;
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    overflow: hidden;
    z-index: 10;
}
.abs_black {
    width: 200%;
    height: 250%;
    background: #000;
    z-index: 15;
    position: absolute;
    left: 0;
    opacity: 0.5;
    top: 0;
}
.about_us__black {
    width: 100% !important;
    height: 350%;
}
.send_request__container {
    position: absolute;
    z-index: 16;
    max-width: 745px;
    top: 5%;
    left: 25%;
    display: grid;
    background: #ffffff;
    border-radius: 10px;
    input[type="text"] {
        height: 60px;
        border-radius: 10px;
        margin: 0 0 20px;
        border: 1px solid #aaabaa;
        padding: 10px 20px;
        box-sizing: border-box;
        font-size: 16px;
        line-height: 19px;
        color: #aaabaa;

        &::placeholder {
            font-size: 16px;
            line-height: 19px;
            color: #aaabaa;
        }
    }
    textarea {
        height: 125px;
        border: 1px solid #aaabaa;
        font-size: 16px;
        line-height: 19px;
        color: #aaabaa;
        padding: 10px 20px;
        box-sizing: border-box;
        &::placeholder {
            font-size: 16px;
            line-height: 19px;
            color: #aaabaa;
        }
    }
}
.close {
    justify-self: end;
    margin: -15px -15px 0 0;
    cursor: pointer;
    width: 50px;
    height: 50px;
    display: flex;
    justify-content: center;
    align-items: center;
    color: #070707;
    background: $col;
    border-radius: 50%;
    font-size: 30px;
    p {
        margin-top: -5px;
    }
}
.send_request__content {
    display: grid;
    padding: 0px 30px 50px;
}
.send_request__title {
    font-weight: bold;
    font-size: 36px;
    line-height: 44px;
    margin: 0 0 50px 0;

    color: #000000;
}
.send_request__text {
    margin: 9px 0 20px;
    font-size: 16px;
    line-height: 19px;
    color: #000000;
}
.radio {
    margin: 20px 0;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    align-items: center;
    label {
        font-size: 14px;
        line-height: 17px;
        color: #000000;
        position: relative;
        display: flex;
        align-items: center;
        a {
            font-size: 14px;
            line-height: 17px;
            color: #000000;
        }
        &::before {
            content: "";
            display: block;
            width: 14px;
            height: 14px;
            border: 1px solid #aaabaa;
            box-sizing: border-box;
            margin-right: 10px;
            border-radius: 50%;
        }
        &::after {
            position: absolute;
            content: "";
            display: none;
            width: 6px;
            height: 6px;
            background: $col;
            border-radius: 50%;
            left: 4px;
        }
    }
}
#send_request_check {
    display: none;
    &:checked {
        & + label::after {
            display: block;
        }
    }
}
.button {
}
.send_request__button {
}

