.slick-slider {
	position: relative;

	.slick-track,
	.slick-list {
		-webkit-transform: translate3d(0, 0, 0);
		-moz-transform: translate3d(0, 0, 0);
		-ms-transform: translate3d(0, 0, 0);
		-o-transform: translate3d(0, 0, 0);
		transform: translate3d(0, 0, 0);
	}

	.slick-list {
		position: relative;
		overflow: hidden;
		width: 100%;
	}

	.slick-track {
		position: relative;
		width: 100%;
		display: flex;
	}

	.slick-slide {
		position: relative;
	}

	.slick-prev,
	.slick-next {
		text-indent: -10000px;
		position: absolute;
		top: 50%;
		width: 50px;
		height: 50px;
		border-radius: 50%;
		display: flex;
		justify-content: center;
		align-items: center;
		background-position: center;
		background-size: 100%;
		transition: all 0.2s ease-out;
		z-index: 1;
	}

	.slick-prev {
		transform: translateY(-50%);
		left: -5px;
		&::after {
			content: "";

			top: 30%;
			left: 35%;
			position: absolute;
			display: block;
			width: 60px;
			height: 60px;
			background-size: cover;
			background: url("../img/prev.svg") no-repeat;
		}
	}

	.slick-next {
		transform: translateY(-50%);
		right: -10px;
		&::after {
			position: absolute;
			top: 30%;
			left: 45%;

			content: "";
			display: block;
			width: 60px;
			height: 60px;
			background-size: cover;
			background: url("../img/next.svg") no-repeat;
		}
	}
	.btn-active {
		background: $col;
	}
	.slick-dots {
		display: flex;
		position: absolute;
		bottom: 15px;
		left: 50%;
		transform: translateX(-50%);

		li {
			margin: 0 5px;

			button {
				text-indent: -9999px;
				width: 10px;
				height: 10px;
				border-radius: 50%;
				background: #fff;
			}

			&.slick-active {
				button {
				}
			}
		}
	}
}
